.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: #f3f3f3;
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px ;
}
 
.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.right { 
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}


/* MEDIA SCREEN */


@media screen and (max-width:768px) {
    .footer-container{
        grid-template-columns:1fr;
    }
}



@media screen and (max-width:320px ) {
    .footer-container{
        grid-template-columns:1fr;
    }
}

