@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');


body{
    background-color: white;
    
}

.header{
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 70px;
    z-index: 10;
}
.header-bg{
    background-color: rgba(0, 0, 0, .2);
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    transition: 0.5s;
}

.nav-menu{
    display: flex;

}

.nav-menu li{
    padding: 0 1rem;
    list-style: none;
} 

.nav-menu li a{

    font-size: 1.5rem;
    font-weight: 500;

}


.hamburger{
    display: none;
}





@media screen and (max-width: 1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.483);
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: ease-in-out 0.5s;

    } 
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a{
        font-size: 2rem ;
    } 
    .hamburger{
        display: initial;
    }
}

@media screen and (max-width:568px) {
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a{
        font-size: 1.5rem ;
    } 
    
}
